    /**
     * 
     * @param isLong  false
     * @param type  
     * @param callback 
     */
    vibrate(isLong: boolean = false, type = null, callback = null) {
        let callFunc = (param) => {
            if (callback) {
                callback(param);
            }
        }
        let shorkOpen = LocalStorage.get(StoreKey.SYS_Shock, true);
        if (!shorkOpen) {
            return;
        }
        switch (this._currowPlatForm) {
            case sys.Platform.WECHAT_GAME:
                if (isLong == true) {
                    WXSdk.instance.vibrateLong(
                        (param) => {
                            Logger.logPlatform("", param);
                            callFunc(param);
                        },
                        (param) => {
                            Logger.logPlatform("", param);
                            callFunc(param);
                        },
                    );
                } else {
                    type = type || WXSdk_VibrateShortType.heavy;
                    WXSdk.instance.vibrateShort(type,
                        (param) => {
                            Logger.logPlatform("", type, param);
                            callFunc(param);
                        },
                        (param) => {
                            Logger.logPlatform("", type, param);
                            callFunc(param);
                        },
                    );
                }
                break;
            case sys.Platform.DESKTOP_BROWSER:
                break;
            default:
                break;
        }
    }
